//--sb-button-group-gap

.sb-button-group {
  align-items: center;
  display: flex;
  gap: var(--sb-button-group-gap, 1rem);
  padding: 0.5rem 0;
}

.sb-button-group_segmented {
  --sb-button-group-gap: 0;

  .sb-button {
    border-radius: 0;

    &:first-child {
      border-radius: var(--sbt-border-radius-small) 0 0
        var(--sbt-border-radius-small);
    }

    &:last-child {
      border-radius: 0 var(--sbt-border-radius-small)
        var(--sbt-border-radius-small) 0;
    }
  }

  &.sb-button-group_solid .sb-button {
    border-left-color: rgba(#fff, 0.5);
    border-left-style: solid;
    border-width: 1px;

    &:hover {
      border-left-color: rgba(#fff, 0.5);
    }

    &:first-child {
      border-left: 1px solid transparent;
    }

    &.sb-button_standard:first-child {
      border-left-color: var(--sb-control-color-primary);
    }
  }

  .sb-button_standard {
    border-left-width: 0;

    &:first-child {
      border-left-width: 2px;
    }
  }

  &.sb-button-group_outline .sb-button {
    border-width: 1px 1px 1px 0;

    &:first-child {
      border-left-width: 1px;
    }
  }

  .sb-control {
    border-radius: 0;
    border-width: 1px 1px 1px 0;
    margin: 0;

    &:first-child {
      border-left-width: 1px;
      border-radius: var(--sbt-border-radius-large) 0 0
        var(--sbt-border-radius-large);
    }

    &:last-child {
      border-radius: 0 var(--sbt-border-radius-large)
        var(--sbt-border-radius-large) 0;
    }
  }

  .sb-control_selected:not(:last-child) {
    border-right: 1px solid rgba(#fff, 0.5);
  }

  .sb-control_unselected {
    border-color: var(--sb-control-color-primary);
  }
}
